import { AfterViewInit, Component, OnInit } from '@angular/core';
import { AuthService } from '../core/auth.service';
import { PetService } from '../core/pet.service';
import { UtilityService } from '../core/utility.service';
import { ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-profile-details',
  templateUrl: './profile-details.page.html',
  styleUrls: ['./profile-details.page.scss'],
})
export class ProfileDetailsPage implements OnInit {
  profiletab: string = 'Basic';
  pets;
  user_role: any = JSON.parse(localStorage.getItem('userObj')).role;
  profileData: any; //= JSON.parse(localStorage.getItem('userObj'));
  constructor(
    private petService: PetService,
    private utility: UtilityService,
    private auth: AuthService,
    private router: ActivatedRoute
  ) {
    console.log('contructor');
  }
  ionViewWillEnter() {
    //your methods
    console.log('entering');
  }

  ngOnInit() {
    this.utility.present();
    this.router.params.subscribe((res) => {
      console.log('working');
      this.getProfileData();
      this.getPets();
    });
    // this.getPets();
  }

  getPets() {
    this.petService.petGets().subscribe((res) => {
      console.log(res);
      this.pets = res;
    });
  }

  openPage(id) {
    this.utility.navigate('/add-edit-pet/' + id);
  }

  getProfileData() {
    let userid = JSON.parse(localStorage.getItem('userObj'))._id;
    this.auth.getProfile({ id: userid }).subscribe((res) => {
      console.log(res);
      this.profileData = res;
      this.utility.setLocal('userObj', JSON.stringify(res));
      this.utility.setSideMenuUserDetail();

      this.utility.dismiss();
    });
  }

  goBack() {
    this.utility.goBack();
  }

  logout() {
    localStorage.clear();
    this.utility.navigateRoot('/login');
  }
}
